<template>
	<div>
		<!---->
		<Breadcrumb parentTitle=""/>

		<el-form :model="form" label-position="top">
			<el-form-item label="" prop="content">
				<mavon-editor v-model="form.content"/>
			</el-form-item>

			<el-form-item label="" prop="likes" style="width: 50%">
				<el-input v-model="form.likes" type="number" placeholder=" 0"></el-input>
			</el-form-item>

			<el-form-item label="" prop="createTime">
				<el-date-picker v-model="form.createTime" type="datetime" placeholder="" :editable="false"></el-date-picker>
			</el-form-item>

			<el-form-item style="text-align: right;">
				<el-button type="info" @click="submit(false)"></el-button>
				<el-button type="primary" @click="submit(true)"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	import Breadcrumb from "@/components/Breadcrumb";
	import {getMomentById, saveMoment, updateMoment} from "@/api/moment";

	export default {
		name: "WriteMoment",
		components: {Breadcrumb},
		data() {
			return {
				form: {
					content: '',
					createTime: null,
					likes: 0,
					published: false
				},
			}
		},
		created() {
			if (this.$route.params.id) {
				this.getMoment(this.$route.params.id)
			}
		},
		methods: {
			getMoment(id) {
				getMomentById(id).then(res => {
					if (res.code === 200) {
						this.form = res.data
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			submit(published) {
				this.form.published = published
				if (this.$route.params.id) {
					updateMoment(this.form).then(res => {
						if (res.code === 200) {
							this.msgSuccess(res.msg)
							this.$router.push('/moments')
						} else {
							this.msgError(res.msg)
						}
					}).catch(() => {
						this.msgError('')
					})
				} else {
					saveMoment(this.form).then(res => {
						if (res.code === 200) {
							this.msgSuccess(res.msg)
							this.$router.push('/moments')
						} else {
							this.msgError(res.msg)
						}
					}).catch(() => {
						this.msgError('')
					})
				}
			}
		}
	}
</script>

