<template>
	<div>
		<!---->
		<Breadcrumb parentTitle=""/>

		<!---->
		<el-row>
			<el-col :span="8">
				<el-input placeholder="" v-model="queryInfo.title" :clearable="true" @clear="search" @keyup.native.enter="search" size="small" style="min-width: 500px">
					<el-select v-model="queryInfo.categoryId" slot="prepend" placeholder="" :clearable="true" @change="search" style="width: 160px">
						<el-option :label="item.name" :value="item.id" v-for="item in categoryList" :key="item.id"></el-option>
					</el-select>
					<el-button slot="append" icon="el-icon-search" @click="search"></el-button>
				</el-input>
			</el-col>
		</el-row>

		<el-table :data="blogList">
			<el-table-column label="" type="index" width="50"></el-table-column>
			<el-table-column label="" prop="title" show-overflow-tooltip></el-table-column>
			<el-table-column label="" prop="category.name" width="150"></el-table-column>
			<el-table-column label="" width="80">
				<template v-slot="scope">
					<el-switch v-model="scope.row.top" @change="blogTopChanged(scope.row)"></el-switch>
				</template>
			</el-table-column>
			<el-table-column label="" width="80">
				<template v-slot="scope">
					<el-switch v-model="scope.row.recommend" @change="blogRecommendChanged(scope.row)"></el-switch>
				</template>
			</el-table-column>
			<el-table-column label="" width="100">
				<template v-slot="scope">
					<el-link icon="el-icon-edit" :underline="false" @click="editBlogVisibility(scope.row)">
						{{ scope.row.published ? (scope.row.password !== '' ? '' : '') : '' }}
					</el-link>
				</template>
			</el-table-column>
			<el-table-column label="" width="170">
				<template v-slot="scope">{{ scope.row.createTime | dateFormat }}</template>
			</el-table-column>
			<el-table-column label="" width="170">
				<template v-slot="scope">{{ scope.row.updateTime | dateFormat }}</template>
			</el-table-column>
			<el-table-column label="" width="200">
				<template v-slot="scope">
					<el-button type="primary" icon="el-icon-edit" size="mini" @click="goBlogEditPage(scope.row.id)"></el-button>
					<el-popconfirm title="" icon="el-icon-delete" iconColor="red" @onConfirm="deleteBlogById(scope.row.id)">
						<el-button size="mini" type="danger" icon="el-icon-delete" slot="reference"></el-button>
					</el-popconfirm>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum"
		               :page-sizes="[10, 20, 30, 50]" :page-size="queryInfo.pageSize" :total="total"
		               layout="total, sizes, prev, pager, next, jumper" background>
		</el-pagination>

		<!---->
		<el-dialog title="" width="30%" :visible.sync="dialogVisible">
			<!---->
			<el-form label-width="50px" @submit.native.prevent>
				<el-form-item>
					<el-radio-group v-model="radio">
						<el-radio :label="1"></el-radio>
						<el-radio :label="2"></el-radio>
						<el-radio :label="3"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" v-if="radio===3">
					<el-input v-model="visForm.password"></el-input>
				</el-form-item>
				<el-form-item v-if="radio!==2">
					<el-row>
						<el-col :span="6">
							<el-switch v-model="visForm.appreciation" active-text=""></el-switch>
						</el-col>
						<el-col :span="6">
							<el-switch v-model="visForm.recommend" active-text=""></el-switch>
						</el-col>
						<el-col :span="6">
							<el-switch v-model="visForm.commentEnabled" active-text=""></el-switch>
						</el-col>
						<el-col :span="6">
							<el-switch v-model="visForm.top" active-text=""></el-switch>
						</el-col>
					</el-row>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="dialogVisible=false"> </el-button>
				<el-button type="primary" @click="saveVisibility"></el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import Breadcrumb from "@/components/Breadcrumb";
	import {getDataByQuery, deleteBlogById, updateTop, updateRecommend, updateVisibility} from '@/api/blog'

	export default {
		name: "BlogList",
		components: {Breadcrumb},
		data() {
			return {
				queryInfo: {
					title: '',
					categoryId: null,
					pageNum: 1,
					pageSize: 10
				},
				blogList: [],
				categoryList: [],
				total: 0,
				dialogVisible: false,
				blogId: 0,
				radio: 1,
				visForm: {
					appreciation: false,
					recommend: false,
					commentEnabled: false,
					top: false,
					published: false,
					password: '',
				}
			}
		},
		created() {
			this.getData()
		},
		methods: {
			getData() {
				getDataByQuery(this.queryInfo).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
						this.blogList = res.data.blogs.list
						this.categoryList = res.data.categories
						this.total = res.data.blogs.total
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			search() {
				this.queryInfo.pageNum = 1
				this.queryInfo.pageSize = 10
				this.getData()
			},
			//
			blogTopChanged(row) {
				updateTop(row.id, row.top).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			//
			blogRecommendChanged(row) {
				updateRecommend(row.id, row.recommend).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			//
			editBlogVisibility(row) {
				this.visForm = {
					appreciation: row.appreciation,
					recommend: row.recommend,
					commentEnabled: row.commentEnabled,
					top: row.top,
					published: row.published,
					password: row.password,
				}
				this.blogId = row.id
				this.radio = this.visForm.published ? (this.visForm.password !== '' ? 3 : 1) : 2
				this.dialogVisible = true
			},
			//
			saveVisibility() {
				if (this.radio === 3 && (this.visForm.password === '' || this.visForm.password === null)) {
					return this.msgError("")
				}
				if (this.radio === 2) {
					this.visForm.appreciation = false
					this.visForm.recommend = false
					this.visForm.commentEnabled = false
					this.visForm.top = false
					this.visForm.published = false
				} else {
					this.visForm.published = true
				}
				if (this.radio !== 3) {
					this.visForm.password = ''
				}
				updateVisibility(this.blogId, this.visForm).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg)
						this.getData()
						this.dialogVisible = false
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			// pageSize 
			handleSizeChange(newSize) {
				this.queryInfo.pageSize = newSize
				this.getData()
			},
			//
			handleCurrentChange(newPage) {
				this.queryInfo.pageNum = newPage
				this.getData()
			},
			goBlogEditPage(id) {
				this.$router.push(`/blogs/edit/${id}`)
			},
			deleteBlogById(id) {
				this.$confirm('<strong style="color: red"></strong>?<br><strong style="color: red"></strong>', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					dangerouslyUseHTMLString: true
				}).then(() => {
					deleteBlogById(id).then(res => {
						if (res.code === 200) {
							this.msgSuccess(res.msg)
							this.getData()
						} else {
							this.msgError(res.msg)
						}
					}).catch(() => {
						this.msgError("")
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					})
				})
			}
		}
	}
</script>

<style scoped>
	.el-button + span {
		margin-left: 10px;
	}
</style>