<template>
	<div>
		<!---->
		<Breadcrumb parentTitle=""/>

		<el-form inline>
			<el-form-item>
				<el-button type="primary" size="mini" icon="el-icon-plus" @click="addDialogVisible=true"></el-button>
			</el-form-item>
			<el-form-item>
				<el-button type="warning" size="mini" icon="el-icon-document-checked" @click="goLogPage"></el-button>
			</el-form-item>
		</el-form>

		<el-table :data="jobList">
			<el-table-column label="id" prop="jobId" width="80"></el-table-column>
			<el-table-column label="bean" prop="beanName"></el-table-column>
			<el-table-column label="" prop="methodName"></el-table-column>
			<el-table-column label="" prop="params"></el-table-column>
			<el-table-column label="cron" prop="cron"></el-table-column>
			<el-table-column label="" width="80">
				<template v-slot="scope">
					<el-switch v-model="scope.row.status" @change="jobStatusChanged(scope.row)"></el-switch>
				</template>
			</el-table-column>
			<el-table-column label="" prop="remark"></el-table-column>
			<el-table-column label="" width="170">
				<template v-slot="scope">{{ scope.row.createTime | dateFormat }}</template>
			</el-table-column>
			<el-table-column label="" width="290">
				<template v-slot="scope">
					<el-button type="warning" icon="el-icon-caret-right" size="mini" @click="runOnce(scope.row.jobId)"></el-button>
					<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
					<el-popconfirm title="" icon="el-icon-delete" iconColor="red" @onConfirm="deleteJobById(scope.row.jobId)">
						<el-button size="mini" type="danger" icon="el-icon-delete" slot="reference"></el-button>
					</el-popconfirm>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum"
		               :page-sizes="[10, 20, 30, 50]" :page-size="queryInfo.pageSize" :total="total"
		               layout="total, sizes, prev, pager, next, jumper" background>
		</el-pagination>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="addDialogVisible" :close-on-click-modal="false" @close="addDialogClosed">
			<!---->
			<el-form :model="addForm" :rules="formRules" ref="addFormRef" label-width="80px">
				<el-form-item label="bean" prop="beanName">
					<el-input v-model="addForm.beanName"></el-input>
				</el-form-item>
				<el-form-item label="" prop="methodName">
					<el-input v-model="addForm.methodName"></el-input>
				</el-form-item>
				<el-form-item label="" prop="params">
					<el-input v-model="addForm.params"></el-input>
				</el-form-item>
				<el-form-item label="cron" prop="cron">
					<el-input v-model="addForm.cron"></el-input>
				</el-form-item>
				<el-form-item label="" prop="remark">
					<el-input v-model="addForm.remark"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="addDialogVisible=false"> </el-button>
				<el-button type="primary" @click="addJob"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="editDialogVisible" :close-on-click-modal="false" @close="editDialogClosed">
			<!---->
			<el-form :model="editForm" :rules="formRules" ref="editFormRef" label-width="80px">
				<el-form-item label="bean" prop="beanName">
					<el-input v-model="editForm.beanName"></el-input>
				</el-form-item>
				<el-form-item label="" prop="methodName">
					<el-input v-model="editForm.methodName"></el-input>
				</el-form-item>
				<el-form-item label="" prop="params">
					<el-input v-model="editForm.params"></el-input>
				</el-form-item>
				<el-form-item label="cron" prop="cron">
					<el-input v-model="editForm.cron"></el-input>
				</el-form-item>
				<el-form-item label="" prop="remark">
					<el-input v-model="editForm.remark"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editJob"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import Breadcrumb from "@/components/Breadcrumb";
	import {getJobList, updateJobStatus, runJobOnce, deleteJobById, addJob, editJob} from "@/api/schedule";

	export default {
		name: "ScheduleJobList",
		components: {Breadcrumb},
		data() {
			return {
				queryInfo: {
					pageNum: 1,
					pageSize: 10
				},
				jobList: [],
				total: 0,
				addDialogVisible: false,
				editDialogVisible: false,
				addForm: {
					beanName: '',
					methodName: '',
					params: '',
					cron: '',
					remark: ''
				},
				editForm: {},
				formRules: {
					beanName: [{required: true, message: 'bean', trigger: 'blur'}],
					methodName: [{required: true, message: '', trigger: 'blur'}],
					cron: [{required: true, message: 'cron', trigger: 'blur'}],
				}
			}
		},
		created() {
			this.getData()
		},
		methods: {
			getData() {
				getJobList(this.queryInfo).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg)
						this.jobList = res.data.list
						this.total = res.data.total
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			handleSizeChange(newSize) {
				this.queryInfo.pageSize = newSize
				this.getData()
			},
			handleCurrentChange(newPage) {
				this.queryInfo.pageNum = newPage
				this.getData()
			},
			jobStatusChanged(row) {
				let text = row.status ? '' : ''
				this.$confirm(`<strong style="color: red">${text}</strong>?`, '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					dangerouslyUseHTMLString: true
				}).then(() => {
					updateJobStatus(row.jobId, row.status).then(res => {
						if (res.code === 200) {
							this.msgSuccess(res.msg)
						} else {
							this.msgError(res.msg)
						}
					}).catch(() => {
						this.msgError("")
					})
				}).catch(() => {
					row.status = !row.status
				})
			},
			runOnce(jobId) {
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					runJobOnce(jobId).then(res => {
						if (res.code === 200) {
							this.msgSuccess(res.msg)
						} else {
							this.msgError(res.msg)
						}
					}).catch(() => {
						this.msgError("")
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					})
				})
			},
			deleteJobById(jobId) {
				deleteJobById(jobId).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg)
						this.getData()
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			showEditDialog(row) {
				this.editForm = {...row}
				this.editDialogVisible = true
			},
			addDialogClosed() {
				this.$refs.addFormRef.resetFields()
			},
			editDialogClosed() {
				this.editForm = {}
				this.$refs.editFormRef.resetFields()
			},
			addJob() {
				this.$refs.addFormRef.validate(valid => {
					if (valid) {
						addJob(this.addForm).then(res => {
							if (res.code === 200) {
								this.msgSuccess(res.msg)
								this.addDialogVisible = false
								this.getData()
							} else {
								this.msgError(res.msg)
							}
						}).catch(() => {
							this.msgError("")
						})
					}
				})
			},
			editJob() {
				this.$refs.editFormRef.validate(valid => {
					if (valid) {
						editJob(this.editForm).then(res => {
							if (res.code === 200) {
								this.msgSuccess(res.msg)
								this.editDialogVisible = false
								this.getData()
							} else {
								this.msgError(res.msg)
							}
						}).catch(() => {
							this.msgError("")
						})
					}
				})
			},
			goLogPage() {
				this.$router.push('/jobs/logs')
			}
		}
	}
</script>

<style scoped>
	.el-button + span {
		margin-left: 10px;
	}

	.el-form--inline .el-form-item {
		margin-bottom: 0;
	}
</style>