<template>
	<div>
		<!---->
		<Breadcrumb parentTitle=""/>

		<!---->
		<el-row>
			<el-col :span="6">
				<el-select v-model="pageId" placeholder="" :filterable="true" :clearable="true" @change="search" size="small" style="min-width: 400px">
					<el-option :label="item.title" :value="item.id" v-for="item in blogList" :key="item.id"></el-option>
				</el-select>
			</el-col>
		</el-row>

		<el-table :data="commentList" row-key="id" :tree-props="{children: 'replyComments'}">
			<el-table-column label="" type="index" width="50"></el-table-column>
			<el-table-column label="" prop="nickname">
				<template v-slot="scope">
					{{ scope.row.nickname }}
					<el-tag v-if="scope.row.adminComment" size="mini" effect="dark" style="margin-left: 5px"></el-tag>
				</template>
			</el-table-column>
			<el-table-column label="" width="80">
				<template v-slot="scope">
					<el-avatar shape="square" :size="60" fit="contain" :src="scope.row.avatar"></el-avatar>
				</template>
			</el-table-column>
			<el-table-column label="" prop="email" show-overflow-tooltip></el-table-column>
			<el-table-column label="" prop="website" show-overflow-tooltip></el-table-column>
			<el-table-column label="ip" prop="ip" width="130"></el-table-column>
			<el-table-column label="" prop="content" show-overflow-tooltip></el-table-column>
			<el-table-column label="QQ" prop="qq" width="115"></el-table-column>
			<el-table-column label="" show-overflow-tooltip>
				<template v-slot="scope">
					<el-link type="success" :href="`/blogs/edit/${scope.row.blog.id}`" target="_blank" v-if="scope.row.page===0">{{ scope.row.blog.title }}</el-link>
					<el-link type="success" :href="'/about'" target="_blank" v-else-if="scope.row.page===1"></el-link>
					<el-link type="success" :href="'/friends'" target="_blank" v-else-if="scope.row.page===2"></el-link>
				</template>
			</el-table-column>
			<el-table-column label="" width="170">
				<template v-slot="scope">{{ scope.row.createTime | dateFormat }}</template>
			</el-table-column>
			<el-table-column label="" width="80">
				<template v-slot="scope">
					<el-switch v-model="scope.row.published" @change="commentPublishedChanged(scope.row)"></el-switch>
				</template>
			</el-table-column>
			<el-table-column label="" width="80">
				<template v-slot="scope">
					<el-switch v-model="scope.row.notice" @change="commentNoticeChanged(scope.row)"></el-switch>
				</template>
			</el-table-column>
			<el-table-column label="" width="200">
				<template v-slot="scope">
					<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
					<el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteCommentById(scope.row.id)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum"
		               :page-sizes="[10, 20, 30, 50]" :page-size="queryInfo.pageSize" :total="total"
		               layout="total, sizes, prev, pager, next, jumper" background>
		</el-pagination>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="editDialogVisible" :close-on-click-modal="false" @close="editDialogClosed">
			<!---->
			<el-form :model="editForm" :rules="formRules" ref="editFormRef" label-width="80px">
				<el-form-item label="" prop="nickname">
					<el-input v-model="editForm.nickname"></el-input>
				</el-form-item>
				<el-form-item label="" prop="avatar">
					<el-input v-model="editForm.avatar"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="" prop="website">
					<el-input v-model="editForm.website"></el-input>
				</el-form-item>
				<el-form-item label="ip" prop="ip">
					<el-input v-model="editForm.ip"></el-input>
				</el-form-item>
				<el-form-item label="" prop="content">
					<el-input v-model="editForm.content" type="textarea" maxlength="250" :rows="5" show-word-limit></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editComment"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import Breadcrumb from "@/components/Breadcrumb";
	import {getCommentListByQuery, getBlogList, updatePublished, updateNotice, deleteCommentById, editComment} from '@/api/comment'
	import {checkEmail} from "@/common/reg";

	export default {
		name: "CommentList",
		components: {
			Breadcrumb
		},
		data() {
			return {
				pageId: null,
				queryInfo: {
					page: null,
					blogId: null,
					pageNum: 1,
					pageSize: 10
				},
				total: 0,
				commentList: [],
				blogList: [],
				editDialogVisible: false,
				editForm: {
					id: null,
					nickname: '',
					avatar: '',
					email: '',
					website: null,
					ip: '',
					content: ''
				},
				formRules: {
					nickname: [{required: true, message: '', trigger: 'blur'}],
					avatar: [{required: true, message: '', trigger: 'blur'}],
					email: [
						{required: true, message: '', trigger: 'blur'},
						{validator: checkEmail, trigger: 'blur'}
					],
					ip: [
						{required: true, message: 'ip', trigger: 'blur'},
						// {validator: checkIpv4, trigger: 'blur'}
					],
					content: [
						{required: true, message: '', trigger: 'blur'},
						{max: 255, message: '255', trigger: 'blur'}
					],
				}
			}
		},
		created() {
			this.getCommentList()
			this.getBlogList()
		},
		methods: {
			getCommentList() {
				getCommentListByQuery(this.queryInfo).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
						this.commentList = res.data.list
						this.total = res.data.total
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			getBlogList() {
				getBlogList().then(res => {
					if (res.code === 200) {
						this.blogList = res.data
						this.blogList.unshift({id: -2, title: ''})
						this.blogList.unshift({id: -1, title: ''})
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			search() {
				if (this.pageId === '') {
					this.queryInfo.page = null
					this.queryInfo.blogId = null
				} else if (this.pageId === -1) {
					this.queryInfo.page = 1
					this.queryInfo.blogId = null
				} else if (this.pageId === -2) {
					this.queryInfo.page = 2
					this.queryInfo.blogId = null
				} else {
					this.queryInfo.page = 0
					this.queryInfo.blogId = this.pageId
				}
				this.queryInfo.pageNum = 1
				this.queryInfo.pageSize = 10
				this.getCommentList()
			},
			// pageSize 
			handleSizeChange(newSize) {
				this.queryInfo.pageSize = newSize
				this.getCommentList()
			},
			//
			handleCurrentChange(newPage) {
				this.queryInfo.pageNum = newPage
				this.getCommentList()
			},
			//
			commentPublishedChanged(row) {
				updatePublished(row.id, row.published).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			//
			commentNoticeChanged(row) {
				updateNotice(row.id, row.notice).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			deleteCommentById(id) {
				this.$confirm('<strong style="color: red"></strong>?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					dangerouslyUseHTMLString: true
				}).then(() => {
					deleteCommentById(id).then(res => {
						if (res.code === 200) {
							this.msgSuccess(res.msg)
							this.getCommentList()
						} else {
							this.msgError(res.msg)
						}
					}).catch(() => {
						this.msgError("")
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
			showEditDialog(row) {
				this.editForm = {...row}
				this.editDialogVisible = true
			},
			editDialogClosed() {
				this.editForm = {}
				this.$refs.editFormRef.resetFields()
			},
			editComment() {
				this.$refs.editFormRef.validate(valid => {
					if (valid) {
						const form = {
							id: this.editForm.id,
							nickname: this.editForm.nickname,
							avatar: this.editForm.avatar,
							email: this.editForm.email,
							website: this.editForm.website,
							ip: this.editForm.ip,
							content: this.editForm.content,
						}
						editComment(form).then(res => {
							if (res.code === 200) {
								this.msgSuccess(res.msg)
								this.editDialogVisible = false
								this.getCommentList()
							} else {
								this.msgError(res.msg)
							}
						}).catch(() => {
							this.msgError("")
						})
					}
				})
			}
		}
	}
</script>

