<template>
	<div>
		<!---->
		<Breadcrumb parentTitle=""/>

		<el-form :model="form" :rules="formRules" ref="formRef" label-position="top">
			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="" prop="title">
						<el-input v-model="form.title" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="URL" prop="firstPicture">
						<el-input v-model="form.firstPicture" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-form-item label="" prop="description">
				<mavon-editor v-model="form.description"/>
			</el-form-item>

			<el-form-item label="" prop="content">
				<mavon-editor v-model="form.content"/>
			</el-form-item>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="" prop="cate">
						<el-select v-model="form.cate" placeholder="" :allow-create="true" :filterable="true" style="width: 100%;">
							<el-option :label="item.name" :value="item.id" v-for="item in categoryList" :key="item.id"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="tagList">
						<el-select v-model="form.tagList" placeholder="" :allow-create="true" :filterable="true" :multiple="true" style="width: 100%;">
							<el-option :label="item.name" :value="item.id" v-for="item in tagList" :key="item.id"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row :gutter="20">
				<el-col :span="8">
					<el-form-item label="" prop="words">
						<el-input v-model="form.words" placeholder="" type="number"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="()" prop="readTime">
						<el-input v-model="form.readTime" placeholder=" Math.round( / 200)" type="number"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="" prop="views">
						<el-input v-model="form.views" placeholder=" 0" type="number"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-form-item style="text-align: right;">
				<el-button type="primary" @click="dialogVisible=true"></el-button>
			</el-form-item>
		</el-form>

		<!---->
		<el-dialog title="" width="30%" :visible.sync="dialogVisible">
			<!---->
			<el-form label-width="50px" @submit.native.prevent>
				<el-form-item>
					<el-radio-group v-model="radio">
						<el-radio :label="1"></el-radio>
						<el-radio :label="2"></el-radio>
						<el-radio :label="3"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" v-if="radio===3">
					<el-input v-model="form.password"></el-input>
				</el-form-item>
				<el-form-item v-if="radio!==2">
					<el-row>
						<el-col :span="6">
							<el-switch v-model="form.appreciation" active-text=""></el-switch>
						</el-col>
						<el-col :span="6">
							<el-switch v-model="form.recommend" active-text=""></el-switch>
						</el-col>
						<el-col :span="6">
							<el-switch v-model="form.commentEnabled" active-text=""></el-switch>
						</el-col>
						<el-col :span="6">
							<el-switch v-model="form.top" active-text=""></el-switch>
						</el-col>
					</el-row>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="dialogVisible=false"> </el-button>
				<el-button type="primary" @click="submit"></el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import Breadcrumb from "@/components/Breadcrumb";
	import {getCategoryAndTag, saveBlog, getBlogById, updateBlog} from '@/api/blog'

	export default {
		name: "WriteBlog",
		components: {Breadcrumb},
		data() {
			return {
				categoryList: [],
				tagList: [],
				dialogVisible: false,
				radio: 1,
				form: {
					title: '',
					firstPicture: '',
					description: '',
					content: '',
					cate: null,
					tagList: [],
					words: null,
					readTime: null,
					views: 0,
					appreciation: false,
					recommend: false,
					commentEnabled: false,
					top: false,
					published: false,
					password: '',
				},
				formRules: {
					title: [{required: true, message: '', trigger: 'change'}],
					firstPicture: [{required: true, message: '', trigger: 'change'}],
					cate: [{required: true, message: '', trigger: 'change'}],
					tagList: [{required: true, message: '', trigger: 'change'}],
					words: [{required: true, message: '', trigger: 'change'}],
				},
			}
		},
		watch: {
			'form.words'(newValue) {
				this.form.readTime = newValue ? Math.round(newValue / 200) : null
			},
		},
		created() {
			this.getData()
			if (this.$route.params.id) {
				this.getBlog(this.$route.params.id)
			}
		},
		methods: {
			getData() {
				getCategoryAndTag().then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
						this.categoryList = res.data.categories
						this.tagList = res.data.tags
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError('')
				})
			},
			getBlog(id) {
				getBlogById(id).then(res => {
					if (res.code === 200) {
						this.computeCategoryAndTag(res.data)
						this.form = res.data
						this.radio = this.form.published ? (this.form.password !== '' ? 3 : 1) : 2
						this.msgSuccess(res.msg)
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError('')
				})
			},
			computeCategoryAndTag(blog) {
				blog.cate = blog.category.id
				blog.tagList = []
				blog.tags.forEach(item => {
					blog.tagList.push(item.id)
				})
			},
			submit() {
				if (this.radio === 3 && (this.form.password === '' || this.form.password === null)) {
					return this.msgError("")
				}
				this.$refs.formRef.validate(valid => {
					if (valid) {
						if (this.radio === 2) {
							this.form.appreciation = false
							this.form.recommend = false
							this.form.commentEnabled = false
							this.form.top = false
							this.form.published = false
						} else {
							this.form.published = true
						}
						if (this.radio !== 3) {
							this.form.password = ''
						}
						if (this.$route.params.id) {
							this.form.category = null
							this.form.tags = null
							updateBlog(this.form).then(res => {
								if (res.code === 200) {
									this.msgSuccess(res.msg)
									this.$router.push('/blogs')
								} else {
									this.msgError(res.msg)
								}
							}).catch(() => {
								this.msgError('')
							})
						} else {
							saveBlog(this.form).then(res => {
								if (res.code === 200) {
									this.msgSuccess(res.msg)
									this.$router.push('/blogs')
								} else {
									this.msgError(res.msg)
								}
							}).catch(() => {
								this.msgError('')
							})
						}
					} else {
						this.dialogVisible = false
						return this.msgError('')
					}
				})
			}
		}
	}
</script>

