<template>
	<div>
		<!---->
		<Breadcrumb parentTitle=""/>

		<!---->
		<el-row :gutter="10">
			<el-col :span="6">
				<el-button type="primary" size="small" icon="el-icon-plus" @click="addDialogVisible=true"></el-button>
			</el-col>
		</el-row>

		<el-table :data="categoryList">
			<el-table-column label="" type="index" width="50"></el-table-column>
			<el-table-column label="" prop="name"></el-table-column>
			<el-table-column label="">
				<template v-slot="scope">
					<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
					<el-popconfirm title="" icon="el-icon-delete" iconColor="red" @onConfirm="deleteCategoryById(scope.row.id)">
						<el-button size="mini" type="danger" icon="el-icon-delete" slot="reference"></el-button>
					</el-popconfirm>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum"
		               :page-sizes="[10, 20, 30, 50]" :page-size="queryInfo.pageSize" :total="total"
		               layout="total, sizes, prev, pager, next, jumper" background>
		</el-pagination>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="addDialogVisible" :close-on-click-modal="false" @close="addDialogClosed">
			<!---->
			<el-form :model="addForm" :rules="formRules" ref="addFormRef" label-width="80px">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="addDialogVisible=false"> </el-button>
				<el-button type="primary" @click="addCategory"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="editDialogVisible" :close-on-click-modal="false" @close="editDialogClosed">
			<!---->
			<el-form :model="editForm" :rules="formRules" ref="editFormRef" label-width="80px">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editCategory"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import Breadcrumb from "@/components/Breadcrumb";
	import {getData, addCategory, editCategory, deleteCategoryById} from '@/api/category'

	export default {
		name: "CategoryList",
		components: {
			Breadcrumb
		},
		data() {
			return {
				queryInfo: {
					pageNum: 1,
					pageSize: 10
				},
				categoryList: [],
				total: 0,
				addDialogVisible: false,
				editDialogVisible: false,
				addForm: {
					name: ''
				},
				editForm: {},
				formRules: {
					name: [{required: true, message: '', trigger: 'blur'}]
				}
			}
		},
		created() {
			this.getData()
		},
		methods: {
			getData() {
				getData(this.queryInfo).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg);
						this.categoryList = res.data.list
						this.total = res.data.total
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			},
			// pageSize 
			handleSizeChange(newSize) {
				this.queryInfo.pageSize = newSize
				this.getData()
			},
			//
			handleCurrentChange(newPage) {
				this.queryInfo.pageNum = newPage
				this.getData()
			},
			addDialogClosed() {
				this.$refs.addFormRef.resetFields()
			},
			editDialogClosed() {
				this.editForm = {}
				this.$refs.editFormRef.resetFields()
			},
			addCategory() {
				this.$refs.addFormRef.validate(valid => {
					if (valid) {
						addCategory(this.addForm).then(res => {
							if (res.code === 200) {
								this.msgSuccess(res.msg)
								this.addDialogVisible = false
								this.getData()
							} else {
								this.msgError(res.msg)
							}
						}).catch(() => {
							this.msgError("")
						})
					}
				})
			},
			editCategory() {
				this.$refs.editFormRef.validate(valid => {
					if (valid) {
						editCategory(this.editForm).then(res => {
							if (res.code === 200) {
								this.msgSuccess(res.msg)
								this.editDialogVisible = false
								this.getData()
							} else {
								this.msgError(res.msg)
							}
						}).catch(() => {
							this.msgError("")
						})
					}
				})
			},
			showEditDialog(row) {
				//row (blogs)()
				//
				this.editForm = {...row}
				this.editDialogVisible = true
			},
			deleteCategoryById(id) {
				deleteCategoryById(id).then(res => {
					if (res.code === 200) {
						this.msgSuccess(res.msg)
						this.getData()
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("")
				})
			}
		}
	}
</script>

<style scoped>
	.el-button + span {
		margin-left: 10px;
	}
</style>